import React, { useState, useContext } from 'react';
import { v4 as uuidv4 } from 'uuid';

import './Form.scss';
import Input from '../Input/Input';
import { RemainderDispatchContext } from '../../contexts/remainder';
import { addRemainder } from '../../reducers/remainderActions';

function Form() {
  const dispatch = useContext(RemainderDispatchContext);
  const [ task, setTask ] = useState('');

  function handleChange(e) {
    setTask(e.target.value);
  }

  function handleSubit(e) {
    e.preventDefault();

    if (!task) return;

    const newTask = [{
      id: uuidv4(),
      task,
      completed: false,
      time: new Date()
    }];
    dispatch(addRemainder(newTask));
    setTask('');
  }

  return(
    <form className='form' onSubmit={handleSubit}>
      <Input type='text' placeholder='add a remainder' change={handleChange} value={task} autoFocus/>
      <i className='fas fa-plus icon' onClick={handleSubit}></i>
    </form>
  );
}

export default Form;