import * as actionType from './reminderActionType';

function toggleTask(state, id) {
  return state.remainder.map(task => task.id === id ? {...task, completed: !task.completed} : task);
}

function deleteTask(state, id) {
  return state.remainder.filter(task => task.id !== id);
}

function editTask(state, id, newTask) {
  return state.remainder.map(task => task.id === id ? {...task, task: newTask, completed: false} : task);
}

function remainderReducer(state, action) {
  switch(action.type) {
    case actionType.ADD_REMAINDER:
      return {
        ...state,
        remainder: action.payload.concat(state.remainder)
      };
    case actionType.TOGGLE_REMAINDER:
      return {
        ...state,
        remainder: toggleTask(state, action.payload)
      };
    case actionType.REMOVE_REMAINDER:
      return {
        ...state,
        remainder: deleteTask(state, action.payload)
      };
    case actionType.EDIT_REMAINDER:
      return {
        ...state,
        remainder: editTask(state, action.id, action.newTask)
      };
    default:
      return state;
  }
}

export default remainderReducer;