import { createContext, useReducer, useEffect } from 'react';
import remainderReducer from '../reducers/remainderReducer';

const initialRem = JSON.parse(window.localStorage.getItem('remainder'));

const initialState = {
  remainder: initialRem || []
};

export const RemainderContext = createContext();
export const RemainderDispatchContext = createContext();

export function RemainderContextProvider(props) {
  const [remainders, dispatch] = useReducer(remainderReducer, initialState);

  useEffect(() => {
    window.localStorage.setItem('remainder', JSON.stringify(remainders.remainder));
  }, [remainders.remainder]);

  return(
    <RemainderContext.Provider value={remainders.remainder}>
      <RemainderDispatchContext.Provider value={dispatch}>
        {props.children}
      </RemainderDispatchContext.Provider>
    </RemainderContext.Provider>
  );
}