import React, { useContext } from 'react';

import './List.scss';
import Item from '../Item/Item';
import { ReactComponent as CloudIcon } from '../../assets/clouds.svg';
import { RemainderContext } from '../../contexts/remainder';

function List() {
  const remainder = useContext(RemainderContext);

  return(
    <div className='list'>
      {
        remainder.length >= 1 ? remainder.map(task => (
          <Item key={task.id} item={task}/>
        )) : <p className='empty__msg'>
          <CloudIcon className='cloud__icon' />
          <span>no remainder</span>
        </p>
      }
    </div>
  );
}

export default List;