import * as actionType from './reminderActionType';

export function addRemainder(newTask) {
  return{
    type: actionType.ADD_REMAINDER,
    payload: newTask
  };
}

export function toggleTask(id) {
  return{
    type: actionType.TOGGLE_REMAINDER,
    payload: id
  };
}

export function deleteTask(id) {
  return{
    type: actionType.REMOVE_REMAINDER,
    payload: id
  };
}

export function editTask(id, newTask) {
  return{
    type: actionType.EDIT_REMAINDER,
    id: id,
    newTask: newTask
  };
}