import React, { useContext, useState, memo } from 'react';

import './Item.scss';
import Input from '../Input/Input';
import { RemainderDispatchContext } from '../../contexts/remainder';
import { toggleTask, deleteTask, editTask } from '../../reducers/remainderActions';
import { genetareTime } from '../../utils/generateTime';

function Item({ item }) {
  const { id, task, completed, time } = item;
  const dispatch = useContext(RemainderDispatchContext);
  const [showForm, setShowForm] = useState(false);
  const [ updatedTask, setUpdatedTask] = useState(task);

  function handleChange(e) {
    setUpdatedTask(e.target.value);
  }

  function toggleForm() {
    setShowForm(true);
  }

  function toggleTaskCompleted(id) {
    dispatch(toggleTask(id));
  }

  function removeTask(id) {
    dispatch(deleteTask(id));
  }

  function updateTask(id, newTask) {
    dispatch(editTask(id, newTask));
    setShowForm(false);
  }

  return(
    <div className='item'>
      <div className='task__check'>
        <input className='checkbox' type="checkbox" checked={completed} onChange={() => toggleTaskCompleted(id)}/>
        {
          showForm ? <Input type='text' change={handleChange} value={updatedTask} autoFocus/> : 
          <div className='task__time'>
            <p className={`${completed && 'task__completed'}`}>{task}</p>
            <p className='date'>{genetareTime(time)}</p>
          </div>
        }
      </div>
      <div className='btns'>
        {showForm ? <i className="fas fa-bookmark icon" onClick={() => updateTask(id, updatedTask)}></i> :
        <>
          <i className="fas fa-pen icon" onClick={toggleForm}></i>
          <i className="fas fa-trash icon" onClick={() => removeTask(id)}></i>
        </>}
      </div>
    </div>
  );
}

export default memo(Item);
